import * as React from "react";
import {Dialog, DialogFooter, DialogType} from 'office-ui-fabric-react/lib/Dialog';
import {DefaultButton} from "office-ui-fabric-react/lib/Button";

export interface IErrorBoxProps {
  message: string;
  onDismiss: Function;
}

export const ErrorBox: React.StatelessComponent<IErrorBoxProps> = (props: IErrorBoxProps) => {
  const showError: boolean = props.message != null;
  const handleClose = () => {
    props.onDismiss();
  };
  if (showError) {
    return (<Dialog
      hidden={!showError}
      onDismiss={handleClose}
      dialogContentProps={{
        type: DialogType.close,
        title: 'Something went wrong',
        subText: props.message
      }}
      modalProps={{
        isBlocking: true,
        containerClassName: 'ms-dialogMainOverride'
      }}
    >
      <DialogFooter>
        <DefaultButton onClick={handleClose} text='Ok'/>
      </DialogFooter>
    </Dialog>);
  } else {
    return null;
  }
};
